import * as React from "react";
import { Card, Button } from "react-bootstrap";

function CardTemplate(props) {
    const { title, image, description, price, handleClick} = props;
    return (
        <Card style={{ width: '15rem', height: 'auto'}}>
            <Card.Img variant="top" src={image} style={{height: '50%', width: '50%', marginLeft: 'auto', marginRight: 'auto'}}/>
            <Card.Body>
                <Card.Title>{title} - {price}</Card.Title>
                <Card.Text>
                    {description}
                </Card.Text>
                <Button variant="primary" onClick={handleClick}>Add to Cart</Button>
            </Card.Body>
        </Card>
    )
}

export default CardTemplate